<template>
  <div class="card">
    <div class="card-body">
      <div class="row">
        <div class="col-xl-12 col-lg-12">
          <div>
            <a-tabs defaultActiveKey="1">
              <a-tab-pane tab="All Users" key="1">
                <router-link v-if="$store.state.user.user.user_type === '1'" to="/dashboard/user/create"><button class="btn btn-info pull-right mb-3 bringToTop">Create User</button></router-link>
                <a-table
                  :columns=" $store.state.user.user.user_type === '1' ? bestsellersColumns : bestsellersColumnsNoAdmin"
                  :dataSource="usersData"
                  class="utils__scrollTable"
                  :scroll="{ x: '100%' }"
                >
                  <span slot="role" slot-scope="text" > {{ text === '1' ? 'Super Admin' : text === '2' ? 'Manager' : text === '3' ? 'Editor' : '' }} </span>
                  <router-link :to="'/dashboard/invoices/' + text.url" class="btn btn-info" slot="action" slot-scope="text" >{{text.name}}</router-link>
                </a-table>
              </a-tab-pane>
            </a-tabs>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
const bestsellersColumns = [
  {
    title: 'ID',
    dataIndex: 'id',
    key: 'id',
    sorter: (a, b) => a.id - b.id,
  },
  {
    title: 'Admin Username',
    dataIndex: 'username',
    key: 'username',
    scopedSlots: { customRender: 'username' },
  },
  {
    title: 'Admin Email',
    dataIndex: 'email',
    key: 'email',
    scopedSlots: { customRender: 'email' },
  },
  {
    title: 'Role',
    dataIndex: 'role',
    key: 'role',
    sorter: (a, b) => a.role - b.role,
    scopedSlots: { customRender: 'role' },
  },
  {
    title: 'Action',
    dataIndex: 'action',
    key: 'action',
    scopedSlots: { customRender: 'action' },
  },
]
const bestsellersColumnsNoAdmin = [
  {
    title: 'ID',
    dataIndex: 'id',
    key: 'id',
    sorter: (a, b) => a.id - b.id,
  },
  {
    title: 'Admin Username',
    dataIndex: 'username',
    key: 'username',
    scopedSlots: { customRender: 'username' },
  },
  {
    title: 'Admin Email',
    dataIndex: 'email',
    key: 'email',
    scopedSlots: { customRender: 'email' },
  },
  {
    title: 'Role',
    dataIndex: 'role',
    key: 'role',
    sorter: (a, b) => a.role - b.role,
    scopedSlots: { customRender: 'role' },
  },
]

export default {
  data: function () {
    return {
      bestsellersColumns,
      bestsellersColumnsNoAdmin,
      usersData: [],
    }
  },
  mounted() {
    this.getUsers()
  },
  methods: {
    moveToCreate() {
      this.$router.push({ name: 'CreateUsers' })
    },
    getUsers() {
      this.axios.get('users/all')
        .then(response => {
          // console.log(response.data.data.users)
          const dt = response.data.data.users
          const data = []
          for (let i = 0; i < dt.length; i++) {
            const userData = {
              username: dt[i].username,
              email: dt[i].email,
              role: dt[i].user_type,
              id: dt[i].id,
              key: dt[i].id,
              action: {
                name: 'Disable',
                url: '/',
              },
            }
            data.push(userData)
          }
          this.usersData = data
        })
        .catch(error => {
          console.log(error)
        })
    },
  },
}
</script>
